import Swiper, { Navigation, Pagination, EffectFade } from 'swiper';
import { Fancybox } from '@fancyapps/ui';
import '@fancyapps/ui/dist/fancybox.css';
import 'swiper/css';
import 'swiper/scss/effect-fade';

import 'ScssComponents/universal/section-head.scss';
import 'ScssComponents/universal/members-slider.scss';
import 'ScssComponents/universal/members-slider-section.scss';
import 'ScssComponents/universal/slider-controls.scss';

import { exist } from 'utils';
import { buildSwiper } from './build-swiper';

const membersSlider = () => {
	const classNames = {
		slider: '.js-member-slider',
		arrowNext: '.js-member-slider-next',
		arrowPrev: '.js-member-slider-prev',
		pagination: '.js-member-slider-pagination',
	};

	const $sliders = document.querySelectorAll(classNames.slider);

	if (!exist($sliders)) return;

	$sliders.forEach(($slider) => {
		const sliderId = $slider.getAttribute('id');

		if (!sliderId) {
			console.log('-----'); //!
			console.error('Please, set unique id for members slider');
			console.log('-----'); //!
			return;
		}
		buildSwiper($slider);

		const sliderInstance = new Swiper($slider, {
			modules: [Navigation, Pagination, EffectFade],
			speed: 400,
			loop: true,
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			navigation: {
				prevEl: `#${sliderId} ${classNames.arrowPrev}`,
				nextEl: `#${sliderId} ${classNames.arrowNext}`,
			},
			pagination: {
				el: `#${sliderId} ${classNames.pagination}`,
				type: 'fraction',
				clickable: true,
			},
			slidesPerView: 1,
		});
	});
};

export default membersSlider;
